import React from "react";
import styled from "styled-components";
import Archive from './Archive';
import Skills from './Skills';

const Space = styled.div`
  background: white;
  height: 550px;
  padding-top: 100px;
`;

const MainOrbit = styled.div`
  border-radius: 50%;
  width: 300px;
  height: 300px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-template-areas:
    ". north-planet . . ."
    ". . . . ."
    ". . sun . east-planet"
    "west-planet . . . ."
    ". . . . south-planet";
  margin: 0 auto;
  justify-items: center;
  align-items: center;
  @keyframes rotate {
    0% { transform: rotate(0deg)}
    50% { transform: rotate(179deg)}
    100% { transform: rotate(359deg)}
  }
  animation-name: rotate;
  animation-duration: 120s;
  animation-iteration-count: infinite;
`;

const Sun = styled.div`
  background-image: radial-gradient(yellow, orange, red);
  border-radius: 50%;
  grid-area: sun;
  height: 5em;
  width: 5em;
`;

const NorthPlanet = styled.div`
  border-radius: 50%;
  grid-area: north-planet;
  height: 3em;
  width: 3em;
  position: relative;
  bottom: 45px;
  cursor: pointer;
  background-image: url("../static/images/planet.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  @keyframes counter-rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(-179deg);
    }
    100% {
      transform: rotate(-359deg);
    }
  }
  animation-name: counter-rotate;
  animation-duration: 120s;
  animation-iteration-count: infinite;
`;

