const Content = styled.div`
  @keyframes aurora {
    0% {
      background-position: left top;
    }
    25% {
      background-position: right top;
    }
    50% {
      background-position: right bottom;
    }
    75% {
      background-position: left bottom;
    }
    100% {
      background-position: left top;
    }
  }
  .outer {
    background: linear-gradient(45deg, #32a6ff 0%, #3f6fff 49%, #8d54ff 82%);
    background-size: 200%;
    animation: aurora 10s infinite;
  }
  .inner {
    background: radial-gradient(#7f7fd5, #86a8e7, #81eae4);
    background-size: 200%;
    animation: aurora 7s infinite
  }
`;

export default class Orbitals extends React.Component{
  state = {
    mount: 'skills',
  };

  handleIconClick = val => {
    this.setState({ mount: val });
  }

  render() {
    let component = <div></div>
    if (this.state.mount === "archive") {
      component = <Archive />
    } else if (this.state.mount === "skills") {
      component = <Skills />
    }
    return <div>
        <Space>
          <MainOrbit>
            <Sun />
            <NorthPlanet className="tooltipped" data-position="top" data-tooltip="About Me" />
            <EastPlanet className="tooltipped" data-position="top" data-tooltip="Projects" />
            <SouthPlanet className="tooltipped" data-position="top" data-tooltip="Skills" />
            <WestPlanet className="tooltipped" data-position="top" data-tooltip="Contact" />
          </MainOrbit>
        </Space>
        <Content>
          <div className="outer">
            <div className="inner">
              {component}
            </div>
          </div>
        </Content>
      </div>;
  }
}
